.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.list {
	display: flex;
	flex-direction: column;
}
.listItem {
	width: 90vw;
	background: #f7f7f7;
	color: #545454;
	text-align: left;
	position: relative;
	padding: 5vw;
}
.listItem:after {
	content: 'â€º';
	position: absolute;
	right: 5vw;
	top: 0vh;
	font-size: 6vh;
}
.listItem h2 {
	margin: 0;
}
.listItem:active {
	background: #e7e7e7;
}